//import { withRouter } from "react-router-dom";
import axios from "axios";
import {HOST} from "../config"

import Select, { components } from "react-select";

class Address extends React.Component {
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      isLoading: true,
      data: [],
      userinfo: {
        isLogin: false,
        userId: "",
        departament: "",
        group: [],
        telephoneNumber: "",
        birthday: "",
        nik: "",
        selectedOption: {
          value: 1,
          label: ''
        }
      },
      dataPlaceholder: '–ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö...'
    };
    
  }


  async componentDidMount() {
    var st = this;
    

    //--- fetch address ---
    st._isMounted = true;

   await axios({
      method: "post",
     url: `${HOST}/api/address.php`
    }).then(res => {
      //console.log(res);
      if (res.status === 200) {
        if (res.data.status === 401) {
          alert("–û—à–∏–±–∫–∞ –∞–≤—Ç—Ä–∏–∑–∞—Ü–∏–∏");
        } else if (res.data.status === 400) {
          alert("–ù–µ–∑–∞–ø–æ–ª–Ω–µ–Ω–æ –æ–¥–Ω–æ –∏–ª–∏ –Ω–µ—Å–∫–æ–ª—å–∫–æ –ø–æ–ª–µ–π");
        } else {
          if (st._isMounted) {
            let rows = [];

            if (Object.keys(res.data).length > 0) {
              Object.keys(res.data).map(index => {
                rows.push(res.data[index]);
                return "succses";
              });
              // console.log(rows)
              this.setState({ data: rows, isLoading: false, dataPlaceholder: '–ü–æ –∑–∞–ø—Ä–æ—Å—É –Ω–µ—Ç —Å–æ–≤–ø–∞–¥–µ–Ω–∏–π' });
              
            }
          }
        }
        return "succses";
      }
    }).then(()=> {
      if(st._isMounted){
        let printBtn = document.getElementById('printBtn')
            printBtn.addEventListener('click', printPage)
      }
    });
  }
  componentWillUnmount(){
    this._isMounted = false;
  }

  filter(e) {
    this.setState({
      filter: e.target.value
    });
  }
  filterDep(e) {
    this.setState({
      filterDep: e.target.value
    });
  }
  outGroupName(g, key) {
    return (
      <div className="table__departRow" key={key}>
        {g}
      </div>
    );
  }

  handleChange = selectedOption => {
    this.setState({ filterDep: selectedOption.value });
  };

  render() {

    const options = [
      {"value":"", "label":"–ü–æ–∫–∞–∑–∞—Ç—å –≤—Å–µ"}
    ];

    const { Option, SingleValue } = components;

    const { selectedOption } = this.state;

    const IconOption = props => (
      <Option {...props}>
        {props.data.label}
      </Option>
    );

    const ValueOption = props => (
      <SingleValue {...props}>
        {props.data.label}
      </SingleValue>
    );
   

    let items = this.state.data;
    let selectItems = this.state.data;
    if (this.state.filter) {
      items = items.filter(
        item =>
          item.cn.toLowerCase().includes(this.state.filter.toLowerCase()) ||
          item.telephoneNumber?.includes(this.state.filter) ||
          item.email?.toLowerCase().includes(this.state.filter.toLowerCase()) ||
          item.ipPhone?.includes(this.state.filter)

          
      );
    }
    if (this.state.filterDep) {
      items = items.filter(item =>
        item.c_department?.toLowerCase().includes(this.state.filterDep.toLowerCase())
      );
    }

    let group = items.reduce((r, a) => {
      r[a.c_department] = [...(r[a.c_department] || []), a];
      return r;
    }, {});
    let select = selectItems.reduce((r, a) => {
      r[a.c_department] = [...(r[a.c_department] || []), a];
      return r;
    }, {});

    Object.keys(select).map((g, key) => (

      options.push({ "value": g, "label": groupName(g)})
    ))

    

    return (
      <div className="content__slide address" id="address">
      
        <div className="slide__nav">
          <h2 className="slide__title">–ê–¥—Ä–µ—Å–Ω–∞—è –∫–Ω–∏–≥–∞</h2>
          {Object.keys(group).length > 0 ? (
            <button id="printBtn">
            –†–∞—Å–ø–µ—á–∞—Ç–∞—Ç—å
          </button>
          ) : (
            <div></div>
          )
          }
        </div>
        <div className="address__head">
          {Object.keys(select).length > 0 ? (
            <div className="table__depart_adr">
              <Select
                className="depart__show_adr"
                value={selectedOption}
                onChange={this.handleChange}
                options={options}
                components={{
                  Option: IconOption,
                  SingleValue: ValueOption
                }}
                placeholder={"–í—ã–±–µ—Ä–∏—Ç–µ –æ—Ç–¥–µ–ª"}
              />
             
            </div>
          ) : (
            <div className="table__depart">
              <div className="depart__show">{this.state.dataPlaceholder}</div>
            </div>
          )}

          <div className="docs__search">
            <input
              type="text"
              className="search__input"
              placeholder="–ü–æ–∏—Å–∫ –ø–æ –∞–¥—Ä–µ—Å–Ω–æ–π –∫–Ω–∏–≥–µ"
              onChange={this.filter.bind(this)}
            />
          </div>
        </div>
        <div className="address__table" id="printTable">
          <div className="table__head">
            <div className="table__row">
              <div className="table__fio">–§–ò–û —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞</div>
              <div className="table__position">–î–æ–ª–∂–Ω–æ—Å—Ç—å</div>
              <div className="table__email">E-mail</div>
              <div className="table__tabel">–í–Ω.–Ω–æ–º–µ—Ä</div>
              <div className="table__tel">–¢–µ–ª–µ—Ñ–æ–Ω</div>
            </div>
          </div>
          <>
            {Object.keys(group).length > 0 ? (
              <div>
                {Object.keys(group).map((g, key) => (
                  <Title name={g} key={key} group={group[g]} />
                ))}
              </div>
            ) : (
                <div className="dataPlaceholder">{this.state.dataPlaceholder}</div>
            )}
          </>
        </div>
        <iframe id="print_frame"></iframe>
      </div>
    );
  }
}
