  Departament_IT:'–î–µ–ø–∞—Ä—Ç–∞–º–µ–Ω—Ç –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã—Ö —Ç–µ—Ö–Ω–æ–ª–æ–≥–∏–π',
  Buhgalterija:'–ë—É—Ö–≥–∞–ª—Ç–µ—Ä–∏—è',  
