  Departament_metodologii_obuchenija_i_razvitija_personala: '–î–µ–ø–∞—Ä—Ç–∞–º–µ–Ω—Ç –º–µ—Ç–æ–¥–æ–ª–æ–≥–∏–∏, –æ–±—É—á–µ–Ω–∏—è –∏ —Ä–∞–∑–≤–∏—Ç–∏—è –ø–µ—Ä—Å–æ–Ω–∞–ª–∞',   
  Direkcija: '–î–∏—Ä–µ–∫—Ü–∏—è',   
