const groupName = (name) => {
  return groupArr[name]
}
const Title = (props) => {
  return (
    <div —Ålass="depart__block">
      <div className="table__departRow" >{groupName(props.name)}</div>
      {props.group.sort((a, b) => (a.cn > b.cn ? 1 : -1)).map((index, key) => <Person key={key} item={index} />)}
  </div>
  )
}
const Person = (props) => {
     return (
       <div className="table__row">
         <div className="table__fio">{props.item.cn}</div>
         <div className="table__position">{props.item.title}</div>
         <div className="table__email">{props.item.email}</div>
         <div className="table__tabel">{props.item.ipPhone}</div>
         <div className="table__tel">{props.item.telephoneNumber}</div>
       </div>
     );
    
}
export default Address;
