import React from "react";
import {
  withRouter
} from "react-router-dom";
import axios from "axios";
import {HOST} from "../config"

//import $ from "jquery";


class Login extends React.Component {
  constructor() {
    super();
    this.state = {
      login: "",
      password: "",
      userinfo: {
        isLogin: false,
        userId:'',
        departament: "",
        group: [],
        telephoneNumber: "",
        birthday: "",
        nik:""
      }
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  }

  authorize = event => {
    event.preventDefault();
    const params = new URLSearchParams();
    params.append("login", this.state.login);
    params.append("password", this.state.password);
    
    axios({
      method: "post",
      url: `${HOST}/api/auth.php`,
      data: params
    })
      .then(res => {
        // console.log(res);
        if (res.status === 200) {
          //console.log(res.status);
         /// console.log(res);

          if (res.data.status === 401) {
            alert("–û—à–∏–±–∫–∞ –∞–≤—Ç—Ä–∏–∑–∞—Ü–∏–∏");
          } else if (res.data.status === 400) {
            alert("–ù–µ–∑–∞–ø–æ–ª–Ω–µ–Ω–æ –æ–¥–Ω–æ –∏–ª–∏ –Ω–µ—Å–∫–æ–ª—å–∫–æ –ø–æ–ª–µ–π");
          }
          else if(res.data.status === 500){
            alert('–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫', res.data.message)
          } else {
            var data = res.data;
            if(data!==''){


            var params = {
              isLogin: true,
              userId: data.id,
              departament: data.departament,
              group: data.group,
              telephoneNumber: data.telephoneNumber,
              birthday: data.birthday,
              nik: data.nik
            };

            localStorage.setItem("profile", JSON.stringify(params));
            this.setState({
              userinfo: {
                isLogin: true,
                userId: data.id,
                departament: data.departament,
                group: data.group,
                telephoneNumber: data.telephoneNumber,
                birthday: data.birthday,
                nik: data.nik
              }
            });
            this.props.updateLogin({
              isLogin: true,
              userId: data.id,
              departament: data.departament,
              group: data.group,
              telephoneNumber: data.telephoneNumber,
              birthday: data.birthday,
              nik: data.nik
            });
            // console.log(res.data.status);
            // console.log(res.data);
            this.props.history.push("/events");
          } else {
            alert("–û—à–∏–±–∫–∞ –∞–≤—Ç—Ä–∏–∑–∞—Ü–∏–∏");
          }
        }
        } else {
          alert("–û—à–∏–±–∫–∞ —Å–µ—Ç–∏, –ø–æ–≤—Ç–æ—Ä–∏—Ç–µ –∑–∞–ø—Ä–æ—Å –ø–æ–∑–¥–Ω–µ–µ");
        }
      })
      .catch(err => console.log(err));   
      
  };

  render() {
    //console.log(this.state.userinfo)
    
    return (
      <div className="content__slide auth" id="auth">
        <div className="auth__wrapper">
        <form onSubmit={this.authorize} >
          <h2 className="auth__title">–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</h2>
          <div className="input__wrapp">
            <span className="input__name">
              –í–≤–µ–¥–∏—Ç–µ –ª–æ–≥–∏–Ω(–ø–µ—Ä–≤–∞—è —á–∞—Å—Ç—å email –¥–æ @) –∏ –≤–∞—à –ø–∞—Ä–æ–ª—å, –∏—Å–ø–æ–ª—å–∑—É–µ–º—ã–π
              –ø—Ä–∏ –≤—Ö–æ–¥–µ –≤ –ü–ö.
            </span>
            <input type="text" className="auth__input" name="login" value={this.state.login}
    onChange={e => this.setState({ login: e.target.value })} />
          </div>
          <div className="input__wrapp">
            <span className="input__name">–ø–∞—Ä–æ–ª—å</span>
            <input type="password" className="auth__input" name="password" value={this.state.password}
    onChange={e => this.setState({ password: e.target.value })} />
          </div>
          <div className="btn__wrapper">
            <button onClick={this.authorize} className="auth__btn">
              –í–æ–π—Ç–∏
            </button>
              <a href="https://password-reset.amulex.ru/ad-user/recovery" target="_blank"  className="auth__forgot">–ù–∞–ø–æ–º–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å</a>
          </div>
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(Login);