import React from "react";
//import dateFns from "date-fns";
import {
  format,
  startOfWeek,
  addDays,
  startOfMonth,
  endOfMonth,
  endOfWeek,
  isSameMonth,
  isSameDay,
  addMonths,
  subMonths
} from "date-fns";
import { ru } from "date-fns/locale";

import "./index.css";

class Calendar extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      currentMonth: new Date(),
      selectedDate: new Date()
    };
  }
  

  componentDidMount(){
    console.log(this.props)
    this.setState({ data: this.props });
  }

  renderHeader() {
    const dateFormat = "MMMM yyyy";

    return (
      <div className="header row flex-middle">
        {/* <div className="col col-start">
          <div className="icon" onClick={this.prevMonth}>
            chevron_left
          </div>
        </div> */}
        <div className="col col-center month__title">
          <span className="disabled__month">
            {format(subMonths(this.state.currentMonth, 1), dateFormat, {
              locale: ru
            })}
          </span>
          <span>
            {format(this.state.currentMonth, dateFormat, { locale: ru })}
          </span>
          <span className="disabled__month">
            {format(addMonths(this.state.currentMonth, 1), dateFormat, {
              locale: ru
            })}
          </span>
        </div>
        {/* <div className="col col-end" onClick={this.nextMonth}>
          <div className="icon">chevron_right</div>
        </div> */}
      </div>
    );
  }

  renderDays() {
    const dateFormat = "EEEEEE";
    const days = [];

    let startDate = startOfWeek(this.state.currentMonth, { locale: ru });

    for (let i = 0; i < 7; i++) {
      //console.log(i)
      
      days.push(
        <div className={`col col-center ${
          i>=5 ? "weekend" : null
        }`} key={i}>
          {format(addDays(startDate, i, { locale: ru }), dateFormat, {
            locale: ru
          })}
        </div>
      );
    }

    return (
      <div className="weeks__wrapper">
        <div className="days row disabled__month">{days}</div>
        <div className="days row">{days}</div>
        <div className="days row disabled__month">{days}</div>
      </div>
    );
  }

  renderCells = (currentMonth, disable, data) => {
   // const {  selectedDate } = this.state;
    const monthStart = startOfMonth(currentMonth, { locale: ru });
    const monthEnd = endOfMonth(monthStart, { locale: ru });
    const startDate = startOfWeek(monthStart, { locale: ru });
    const endDate = endOfWeek(monthEnd);
    //console.log(new Date('2020-02-14'));
    var selectedDateArr  = data

    var selectedDateS = [];
    if (Object.keys(selectedDateArr).length > 0) {
      selectedDateArr.map((index, key) => {
       // console.log(index)
        
        selectedDateS[format(new Date(index), "yyyy-MM-dd")] = new Date(index)
        return "sucsess"
    })}
      

     


    const dateFormat = "d";
    const rows = [];

    let days = [];
    let day = startDate;
    let formattedDate = "";

    while (day <= endDate) {
      for (let i = 0; i < 7; i++) {
        formattedDate = format(day, dateFormat, { locale: ru });
        const curD = format(day, "yyyy-MM-dd");

     
        isSameDay(day, selectedDateS[curD])
          ? days.push(
              <div
                className={`col cell ${
                  !isSameMonth(day, monthStart)
                    ? "disabled"
                    : isSameDay(day, selectedDateS[curD])
                    ? "selected"
                    : ""
                }`}
                key={day}
                onClick={() =>
                  this.props.handler(
                    format(selectedDateS[curD], "yyyy-MM-dd", { locale: ru })
                  )
                }
              >
                <span className="number">{formattedDate}</span>
              </div>
            )
          : days.push(
              <div
                className={`col cell ${
                  !isSameMonth(day, monthStart)
                    ? "disabled"
                    : isSameDay(day, selectedDateS[curD])
                    ? "selected"
                    : ""
                }`}
                key={day}
                onClick={() => console.log("non select")}
              >
                <span className="number">{formattedDate}</span>
              </div>
            );
      
          
          day = addDays(day, 1);

       
       
        
      }
      rows.push(
        <div className="row" key={day}>
          {days}
        </div>
      );
      days = [];
    }
    return <div className={`body ${
      disable ?
      "disabled__month" : null
    }`}>{rows}</div>;
  }

  onDateClick = day => {
    this.setState({
      selectedDate: day
    });
  };

  nextMonth = () => {
    this.setState({
      currentMonth: addMonths(this.state.currentMonth, 1)
    });
  };

  prevMonth = () => {
    this.setState({
      currentMonth: subMonths(this.state.currentMonth, 1)
    });
  };

  render() {
    
    var data = this.props.data ? this.props.data : null;
   // console.log('data ===> ', this.props.data);
    return (
      <div className="calendar">
        <div className="overlay">
          {this.renderHeader()}
          {this.renderDays()}
          {data ?
          <div className="body__wrapper">
            {this.renderCells(subMonths(this.state.currentMonth, 1), true, data)}
            {this.renderCells(this.state.currentMonth, '', data)}
            {this.renderCells(addMonths(this.state.currentMonth, 1), true, data)}

          </div>
          :
          <div>–ó–∞–≥—Ä—É–∑–∫–∞...</div> }
        </div>
      </div>
    );
  }
}

export default Calendar;
