import React from "react";
import $ from "jquery";
import {Line} from 'react-chartjs-2';
import { authContext } from "../../context/auth";

class Stats extends React.Component {
    _isMounted = false;
    urlSales = 'https://sale.amulex.ru/sales/sales-review/get-json-last30';
    urlSert = 'https://lk-office.amulex.ru/OpenStatistics/daysLast30';
    constructor(props) {
        super(props);
        this.state = {
            tab: this.props.tab,
            date: this.props.date,
            dataPlaceholder: '–ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö...',
            salesData : null,
            data : null,
            sertData: null,
            consulData: null
        }
    }
    async componentDidMount() {
        this._isMounted = true;
        let sales = [],
            serts = [],
            consuls = [],
            datePoints = null,
            labels = null;
       
        const url = this.state.tab === 'sale'? this.urlSales : this.urlSert;
        let myData = await fetch(url, {
            cache : "force-cache"
        })
            .then(response => response.json())
            .then(salesData => {
                if(this._isMounted){
                    this._isMounted && this.setState({salesData})
                    const result = this.state.salesData;
                    let currDate = new Date().getMonth();
                    for(let key in result) {
                        let passDate = new Date(key);
                        if (currDate === passDate.getMonth()){
                            if(this.state.tab === 'sale'){
                                sales.push({date: passDate.getDate(), activated :result[key].activated});
                                datePoints = sales.map((el, index)=>{
                                    return el.activated
                                });
                                labels = sales.map((el, index)=>{
                                    if (el.date < 10){
                                        console.log('ssasdas'+passDate.getMonth())
                                        return '0' + el.date + (passDate.getMonth() < 10 ? '.0' + passDate.getMonth() : '.'+ passDate.getMonth())
                                    } else {
                                        return String(el.date) + (passDate.getMonth() < 10 ? '.0' + passDate.getMonth() : '.'+ passDate.getMonth())
                                    }
                                });
                            }
                            if(this.state.tab === 'sertificats'){
                                serts.push({date: passDate.getDate(), activations : result[key].activations});
                                datePoints = serts.map((el, index)=>{
                                    return el.activations
                                });
                                labels = serts.map((el, index)=>{
                                    if (el.date < 10){
                                        return '0' + el.date + (passDate.getMonth() < 10 ? '.0' + passDate.getMonth() : '.'+ passDate.getMonth())
                                    } else {
                                        return String(el.date) + (passDate.getMonth() < 10 ? '.0' + passDate.getMonth() : '.'+ passDate.getMonth())
                                    }
                                });
                            }
                            if(this.state.tab === 'consuls'){
                                consuls.push({date: passDate.getDate(), consuls : result[key].verbal + result[key].written});
                                datePoints = consuls.map((el, index)=>{
                                    return el.consuls
                                });
                                labels = consuls.map((el, index)=>{
                                    if (el.date < 10){
                                        return '0' + el.date + (passDate.getMonth() < 10 ? '.0' + passDate.getMonth() : '.'+ passDate.getMonth())
                                    } else {
                                        return String(el.date) + (passDate.getMonth() < 10 ? '.0' + passDate.getMonth() : '.'+ passDate.getMonth())
                                    }
                                });
                            }

                        }
                    }
                }
            });
            
            if (this._isMounted === true){
                this.setState({
                    data: {
                        labels: labels,
                        datasets: [
                          {
                            label: this.state.tab,
                            fill: false,
                            lineTension: 0.1,
                            backgroundColor: 'rgba(75,192,192,0.4)',
                            borderColor: 'rgba(75,192,192,1)',
                            borderCapStyle: 'butt',
                            borderDash: [],
                            borderDashOffset: 0.0,
                            borderJoinStyle: 'miter',
                            pointBorderColor: 'rgba(75,192,192,1)',
                            pointBackgroundColor: '#fff',
                            pointBorderWidth: 1,
                            pointHoverRadius: 5,
                            pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                            pointHoverBorderColor: 'rgba(220,220,220,1)',
                            pointHoverBorderWidth: 2,
                            pointRadius: 1,
                            pointHitRadius: 10,
                            data: datePoints
                          }
                        ]
                      }
                })            
            }
    }
    componentWillUnmount() {
        this._isMounted = false;
        
    }

    render(){
        return (
            this.state.data ? (
                <div className="date__charts" id="date__charts">
                    <Line ref={this.chartReference} data={this.state.data} legend={false} />
                </div>
            ) : (
                <div className="dataPlaceholder">{this.state.dataPlaceholder}</div>
            )
        );
    }
}

export default Stats;