const OutWeeks = (props) => {

  
  var data = props.data;
  var rows = [];

  var date = new Date(props.num);
  var today = new Date();
  const day = date.getDate();
  const month = mnt[date.getMonth()];
 
  var active =
    today.getDate() === day
      ? "events__item events__item_active"
      : "events__item";
 

  Object.keys(data).length > 0
    ? Object.keys(data).map((g, key) => {
          rows.push(<OutEvent data={data[g]} key={key} type="week" />)
          return "succses";
        }
    )
    : rows.push(<div>–Ω–∞ —ç—Ç–æ–π –Ω–µ–¥–µ–ª–µ –Ω–µ—Ç –º–µ—Ä–æ–ø—Ä–∏—è—Ç–∏–π</div>);
    
   
  return (
    <section className={active}>
      <div className="events__date">
        <span className="date__num">{day}</span>
        <span className="date__mounth">{month}</span>
      </div>
      <div className="events__wrap">{rows}</div>
    </section>
  );
  
}


