const OutMonth = (props) => {
  var today = new Date();
  var year = today.getFullYear();
  var month = [];
  var events = [];
  var dataArr = [];
  var day = '';
  var eventMonth = '';
  //console.log(props.group);

   Object.keys(props.group).map((d, key) => {
  
   if (props.group[d].length > 0) {
     //console.log('d ===>',d)
     var date = new Date(d);
      eventMonth = mnt[date.getMonth() + 1];
     var m = date.getMonth() + 1;
      day = date.getDate();

     var mn = m < 10 ? "0" + m : m;
     day = day < 10 ? "0" + day : day;
     var dates = year + "-" + mn + "-" + day;
     
     //console.log(d);
     Object.keys(props.group[d]).map((item, i) => {
     
       
       dataArr[dates] = props.group[d];
       month.push(dates);
       return 'ok'
      
     });
   }
   return "ok"
  })
  
 //console.log(dataArr[props.events]);
 //console.log('props state ===>', props)

 if (props.events && dataArr[props.events]) {
   var eventDay = new Date(props.events);
    eventMonth = mnt[eventDay.getMonth() ];
    eventDay = eventDay.getDate();
     dataArr[props.events].map((item, key) => {
       events.push(<OutEvent data={item} key={key} editor={props.editor} openMore={props.openMore} state={props.state} delEvent={props.delEvent} editEvent={props.editEvent} showMore={props.showMore}/>);
        return 'ok'
      })
   
 }
 // console.log('len ===>',month.length)
  return (
    <div className="docs__table" id="mounth">
      {month ? (
        <Calendar data={month} handler={props.handler} openModal={props.openModal} />
      ) : (
        <div>–ó–∞–≥—Ä—É–∑–∫–∞</div>
      )}
      {events.length > 0 ? (
        <div className="events__tray">
          <section className="events__item events__item_current">
            <div className="events__date">
              <span className="date__num">{eventDay}</span>
              <span className="date__mounth">{eventMonth}</span>
            </div>
            <div className="events__wrap">
              {events.length > 0 ? events : <div>–ó–∞–≥—Ä—É–∑–∫–∞</div>}
            </div>
          </section>
        </div>
      ) : null}
    </div>
  );
}

