  fetchWeekData = async _isMounted => {
    await axios({
      method: "post",
      url: `${HOST}/api/query.php?act=outbday`
    }).then(res => {
      //console.log(res)
      if (res.status === 200) {
        if (res.data.status === 401) {
          alert("–û—à–∏–±–∫–∞ –∞–≤—Ç—Ä–∏–∑–∞—Ü–∏–∏");
        } else if (res.data.status === 400) {
          alert("–ù–µ–∑–∞–ø–æ–ª–Ω–µ–Ω–æ –æ–¥–Ω–æ –∏–ª–∏ –Ω–µ—Å–∫–æ–ª—å–∫–æ –ø–æ–ª–µ–π");
        } else {
          if (_isMounted) {
            let rows = [];

           // console.log(res.data.result);

            //if(res.data)

            if (
              Object.keys(res.data).length > 0 &&
              res.data.result !== "nodata"
            ) {
              Object.keys(res.data).map(index => {
                rows.push(res.data[index]);
                return "succses";
              });
              //console.log(rows)
              this.setState({ weekData: rows, isLoading: false });
            } else {
              this.setState({ weekData: "nodata", isLoading: false });
            }
          }
        }
        return "succses";
      }
    }).catch(err => {
      console.log(err.message)
      this.setState({ weekData: err.message, isLoading: false });
    }
    );
  };
