    //console.log(nik);
    if(nik){
        nik = nik.split(" ");
        if (nik.length === 3) {
          nik =
            nik[0] +
            " " +
            nik[1].substring(0, 1) +
            "." +
            nik[2].substring(0, 1) +
            ".";
        } else {
          nik = nik[0] + " " + nik[1].substring(0, 1) + ".";
        }
    } else {
      return '';
    }
    

    return nik;
